<template>
    <div id="contain">
        <div class="header">
            <div class="Input">
                <el-input class="title" placeholder="题目" v-model="title" maxlength="15"></el-input>
                <el-input class="publisher" placeholder="发布者" v-model="publisher" maxlength="10"></el-input>
            </div>
            <div class="publish">
                <input accept="*.pdf,*.txt,*.doc" type="file" name="image" @change="getFile($event)" />
                <el-button type="success" round @click="publish()">发布</el-button>
            </div>
        </div>
        <div class="write">
          <quill-editor
            class="mr-8"
            max-height="950"
            v-model="content"
            ref="myQuillEditor"
            :options="editorOption"
            style="height: 750px; width:1300px; margin:20px auto;"
            @change="onEditorChange($event)"
          >
          </quill-editor>
        </div>

    </div>
</template>

<script>
import { quillEditor } from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
export default {
    data(){
        return{
            content:null,
            title:'',
            publisher:'',
            file:null,
            url:'',

            editorOption: {
                theme: 'snow', // or 'bubble'
                modules: {
                toolbar: [
                    ['bold', 'italic', 'underline', 'strike'],
                    ['blockquote', 'code-block'][({ header: 1 }, { header: 2 })],
                    [{ list: 'ordered' }, { list: 'bullet' }],
                    [{ script: 'sub' }, { script: 'super' }],
                    [{ indent: '-1' }, { indent: '+1' }],
                    [{ size: ['small', false, 'large', 'huge'] }],
                    [{ header: [1, 2, 3, 4, 5, 6, false] }],
                    [{ color: [] }],
                    [{ align: [] }],
                    ['link','image'],
                    ['sourceEditor'],
                ],
                },
            },
        }
    },
    methods: {
        
        onEditorChange({ html }) {
            this.content = html
        },
        getFile: function (event) {
            this.file = event.target.files[0];
        },
        publish(){
            if(this.content === null){
                alert('内容不能为空！');
                return;
            }
            else if(this.publisher === ''){
                alert('发布者不能为空！');
                return;
            }
            else if(this.title === ''){
                alert('标题不能为空！');
                return;
            }
            let news={};
            this.$confirm('是否确认发表?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'info'
                }).then(() => {
                    let formdata = new FormData();
                    formdata.append("File",this.file);
                    this.$http.post('boss/upload',formdata).then((res)=>{
                        this.url = res.data.data;
                        news={
                        title:this.title,
                        publisher:this.publisher,
                        context:this.content,
                        fileURL:this.url
                    };
                    this.$http.post('boss/addNews',{news:news}).then(()=>{
                        this.$message({
                            type: 'success',
                            message: '提交成功!'
                            });
                        this.content=null;
                        this.$router.push('/manager/newsmanage')
                    })
                    })
                }).catch(() => {
                this.$message({
                    type: 'warning',
                    message: '发布失败'
                });          
                });
        }
    },
    components: {
        quillEditor
    },

}
</script>

<style scoped>
#contain .write .quill-editor{
    background: white;
}
.left{
    float: left;
}
.header{
    width: 50%;
    height: 120px;
    margin: 10px auto; 
}
.header .el-input{
    margin-top:5px ;
}
#contain{
    width: 100%;
    min-height: 100%;
    background: rgb(234,237,241);
}
#contain  .el-button.is-round{
    margin: 0px 10px;
}
.publish{
    width: 50%;
    position: relative;

    height: 100px;
    left: 80%;
}
Input{
    width: 50%;
}
</style>